<template>
  <va-inner-loading :loading="loading && actions.length">
    <div v-if="actions.length || true" class="tab" :style="computedStyle">
      <div class="tab__actions">
        <va-button
          v-for="action in actions"
          :key="action.name"
          outline
          :color="action.color"
          @click="doAPIRequest(action.url)"
          >{{ $t(action.nameLocalPath) }}</va-button
        >
      </div>
      <div class="tab__send_popup mt-3">
        <va-accordion>
          <va-collapse>
            <span slot="header">
              {{ $t('dashboard.profile.actions.popup.header') }}
            </span>
            <div slot="body">
              <va-select
                :options="['Error', 'Warning', 'Info']"
                value="Error"
                v-model="customPopupType"
                class="ma-0"
                :label="$t('dashboard.profile.actions.popup.type')"
                no-clear
              />
              <va-input
                class="mb-3"
                :label="$t('dashboard.profile.actions.popup.title')"
                v-model="customPopupTitle"
              ></va-input>
              <va-input
                class="mb-3"
                :label="$t('dashboard.profile.actions.popup.message')"
                v-model="customPopupText"
              ></va-input>
              <va-button
                class="mt-2 justify--center"
                color="info"
                @click="sendPopup"
              >
                {{ $t('dashboard.profile.actions.popup.send') }}
              </va-button>
            </div>
          </va-collapse>
        </va-accordion>
      </div>
      <div class="tab__set_withdraw_error mt-3">
        <va-accordion>
          <va-collapse>
            <span slot="header">
              {{ $t('dashboard.profile.actions.withdraw-error.title') }}
            </span>
            <div slot="body">
              <va-input
                class="mb-3"
                :placeholder="
                  $t('dashboard.profile.actions.withdraw-error.placeholder')
                "
                v-model="customWithdrawError"
                :resize="false"
                height="6rem"
                type="textarea"
              ></va-input>
              <va-button
                class="mt-2 justify--center"
                color="info"
                @click="setWithwrawError"
              >
                {{ $t('dashboard.profile.actions.withdraw-error.set') }}
              </va-button>
            </div>
          </va-collapse>
        </va-accordion>
      </div>
    </div>
    <VaCardNoData
      v-else
      :loading="loading"
      loading-text="Actions are loading..."
      empty-text="Nothing to do"
    />
  </va-inner-loading>
</template>

<script>
import VaCardNoData from '../components/VaCardNoData'
import axios from 'axios'
import * as Users from '@/api/Users'

export default {
  components: {
    VaCardNoData,
  },
  props: {
    loading: Boolean,
    me: Object,
    user: Object,
  },
  data: () => ({
    customPopupType: 'Error',
    customPopupTitle: '',
    customPopupText: '',
    customWithdrawError: '',
  }),
  computed: {
    actions() {
      return this.user.actions
    },
    computedStyle() {
      return {
        opacity: this.loading ? 0.2 : 1,
      }
    },
  },
  watch: {
    'user.customWithdrawError': 'updateError',
  },
  methods: {
    sendPopup() {
      if (this.loading) {
        this.handleSuccess({
          message: 'Please wait until the last operation is done.',
        })
        return
      }

      this.loading = true

      Users.callPopup(
        this.user.id,
        this.customPopupType,
        this.customPopupTitle,
        this.customPopupText
      )
        .then(({ data }) => {
          this.loading = false
          this.handleSuccess({
            message: 'Popup successfully sent!',
          })
        })
        .catch(err => {
          this.loading = false
          this.handleSuccess({
            message: 'Popup has not sent.',
          })
        })
    },
    setWithwrawError() {
      if (this.loading) {
        this.handleSuccess({
          message: 'Please wait until the last operation is done.',
        })
        return
      }

      this.loading = true

      Users.setWithwrawError(this.user.id, this.customWithdrawError)
        .then(({ data }) => {
          this.user.customWithdrawError = this.customWithdrawError
          this.loading = false
          this.handleSuccess({
            message: 'Custom widthdraw error successfully set!',
          })
        })
        .catch(err => {
          this.loading = false
          this.handleError({
            message: 'Custom widthdraw error has not set.',
          })
        })
    },
    updateError(v) {
      this.customWithdrawError = v
    },
    handleSuccess(response) {
      this.showToast(response.message, {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'success',
      })
    },
    handleError(response) {
      this.showToast(response.message, {
        icon: 'fa-times',
        position: 'bottom-center',
        duration: 4000,
        type: 'primary',
      })
    },
    doAPIRequest(url) {
      const self = this
      this.loading = true

      axios
        .get(window.location.protocol + '//' + window.location.host + url)
        .then(({ data: response }) => {
          this.$root.$emit('update-users')

          setTimeout(() => {
            self.$root.$emit('getting-profile')
            self.$root.$emit('refresh-profile')
          }, 40)

          if (response.success) {
            if (response.action === 'sign in as') {
              localStorage.setItem('auth-token', response.token)
              localStorage.setItem('admin-token', response.admin)
              window.location.assign(
                window.location.protocol + '//' + window.location.host
              )
            } else {
              this.showToast(response.message || `Succeed!`, {
                icon: 'fa-check',
                position: 'bottom-center',
                duration: 4000,
                type: 'success',
              })
            }
          } else {
            this.showToast(response.message || `Failed.`, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
        .catch(err => {
          var err = err.response
            ? err.response.data.message +
              ' Status code ' +
              (err.response ? err.response.status : '%CODE%')
            : err
          this.loading = true
          this.showToast(err, {
            icon: 'fa-times',
            position: 'bottom-center',
            duration: 4000,
            type: 'primary',
          })
        })
    },
  },
  mounted() {
    this.customWithdrawError = this.user.customWithdrawError

    this.$root.$on('update-custom-withdraw-error', this.updateError)
  },
  beforeDestroy() {
    this.$root.$off('update-custom-withdraw-error', this.updateError)
  },
}
</script>

<style lang="scss" scoped>
.tab {
  min-height: 65px;

  &__actions {
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
  }
}
</style>
