import axios from 'axios'

const base = window.location.protocol + '//' + window.location.host

const remove = (id, user) =>
  axios.post(base + '/api/admin/transaction/delete', { id, user })
const removeDeposit = (id, user) =>
  axios.post(base + '/api/admin/deposit/delete', { id, user })
const removeWithdrawal = (id, user) =>
  axios.post(base + '/api/admin/withdrawal/delete', { id, user })
const verifyWithdrawal = (id, user) =>
  axios.get(base + `/api/admin/user/${user}/withdrawal/${id}/verify`)
const create = body => axios.post(base + '/api/admin/transaction/create', body)
const getDepositsList = () => axios.get(base + '/api/admin/deposits')

export {
  remove,
  create,
  removeDeposit,
  getDepositsList,
  verifyWithdrawal,
  removeWithdrawal,
}
