const state = {
  users: null,
  promo: [],
}

const mutations = {
  AUTHS(state, users) {
    state.users = users
  },
  SET_PROMO(state, data) {
    state.promo = data
  },
  ADD_PROMO(state, el) {
    state.promo.unshift(el)
  },
}

const getters = {
  users: state => state.users,
  promo: state => state.promo,
  managers: state =>
    state.users
      ? state.users.filter(u => ['manager', 'owner'].includes(u.role))
      : [],
}

export default {
  state,
  mutations,
  getters,
}
