<template>
  <va-card
    :color="$themes.background"
    :style="computedWidgetStyle"
    :title="$t('dashboard.support.title')"
  >
    <h6
      slot="actions"
      class="user-title"
      :style="{ color: $themes.foreground }"
    >
      <va-icon name="fa fa-user-circle" class="mr-2"></va-icon>
      {{ user.name }}
    </h6>
    <va-inner-loading :loading="!itsAdmin && loading">
      <div v-if="!itsAdmin" class="tab" :style="computedStyle">
        <chat
          @update-messages="updateMessages"
          :value="user.supportMessages"
          @input="sendMessage"
        />
      </div>
      <va-card-no-data
        v-else
        :loading="loading"
        :loading-text="$t('dashboard.no-data.support.loading')"
        :empty-text="$t('dashboard.no-data.support.empty')"
      />
    </va-inner-loading>
  </va-card>
</template>

<script>
import Chat from '../ui/chat/Chat'
import VaCardNoData from './components/VaCardNoData'
import * as Support from '@/api/Support'

export default {
  name: 'dashboard-support',
  components: { Chat, VaCardNoData },
  data: () => ({
    user: {
      id: '',
      name: '',
      supportMessages: [],
    },
    userRaw: null,
    loading: false,
    itsAdmin: true,
    prevTimeout: null,
  }),
  methods: {
    startFetchingMessages() {
      var userWhenStarted = this.user.id

      Support.fetchMessages(userWhenStarted)
        .then(({ data }) => {
          this.prevTimeout = setTimeout(this.startFetchingMessages, 2000)

          if (userWhenStarted != this.user.id) return

          if (this.user.supportMessages.length != data.messages.length) {
            var sending = this.user.supportMessages.filter(m => !m.delivered)

            this.user.supportMessages = [...data.messages, ...sending]
            this.$root.$emit('chat-update')
          }
        })
        .catch(err => {
          this.prevTimeout = setTimeout(this.startFetchingMessages, 20000)
        })
    },
    sendMessage(message) {
      this.user.supportMessages.push(message)
      this.$root.$emit('chat-update')

      Support.sendMessage(this.user.id, message.text).then(({ data }) => {
        message.date = data.message.date
        message.text = data.message.text
        message.yours = data.message.yours
        message.delivered = true
        if (this.userRaw) this.userRaw.unread = 0

        this.user.supportMessages = this.user.supportMessages.slice()

        this.$root.$emit('chat-update')
      })
    },
    gettingDialogue() {
      this.loading = true
    },
    openDialogue({ user, messages }) {
      this.loading = false
      this.itsAdmin = user.role != 'user'
      this.user.id = user.id
      this.user.name = user.name
      this.userRaw = user

      if (!this.itsAdmin) {
        this.user.supportMessages = messages
        this.$root.$emit('chat-update')
        clearTimeout(this.prevTimeout)
        this.prevTimeout = setTimeout(this.startFetchingMessages, 2000)
      }
    },
    updateMessages(messages) {
      Support.updateMessages(messages, this.user.id)
        .then(response => {
          this.showToast('The messages has been updated', {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          if (err.response?.data?.message) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            this.showToast('The messages has not been updated', {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
    },
  },
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    computedWidgetStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
  },
  created() {
    this.$root.$on('open-dialogue', this.openDialogue)
    this.$root.$on('getting-dialogue', this.gettingDialogue)
  },
  beforeDestroy() {
    this.$root.$off('open-dialogue', this.openDialogue)
    this.$root.$off('getting-dialogue', this.gettingDialogue)
  },
}
</script>

<style lang="scss" scoped>
.tab {
  height: 100%;
}

.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.user-title {
  font-size: 0.8rem;
  letter-spacing: 0.06rem;
  font-weight: 500;
  text-transform: uppercase;
}
</style>
