<template>
  <div class="va-chat">
    <div
      class="va-chat__body"
      :style="{ backgroundColor: '#00000017' }"
      v-sticky-scroll="{
        animate: true,
        duration: 200,
      }"
    >
      <div class="va-chat__body-messages">
        <div
          class="va-chat__message"
          v-for="(message, index) in value"
          :key="index"
          :style="{
            ...(message.yours ? computedYoursStyle : computedStyle),
            paddingTop: !message.delivered ? '1.4rem' : '0.657rem',
          }"
          :class="{ 'va-chat__message--yours': message.yours }"
        >
          <div
            class="va-chat__message-info"
            @click="startEditing(message)"
            :style="{
              left: message.yours ? 'auto' : '0.5rem',
              right: message.yours ? '0.5rem' : 'auto',
            }"
          >
            <va-icon
              name="fa fa-spinner fa-spin"
              v-if="!message.delivered"
            ></va-icon>
            <va-icon
              name="fa fa-pencil"
              class="edit-message"
              v-if="message.delivered"
            ></va-icon>
          </div>

          <span class="va-chat__message-text">
            {{ message.text }}
          </span>
          <span
            class="va-chat__message_date"
            :style="{
              opacity: 0.4,
              textAlign: message.yours ? 'right' : 'left',
            }"
          >
            {{ formatDate(message.date) }}
          </span>
        </div>
      </div>
    </div>
    <div class="va-chat__controls">
      <va-input
        v-model="inputMessage"
        @keypress.enter.prevent="sendMessage"
        @keydown.ctrl.enter="() => {
            inputMessage += '\n'
          }"
        :placeholder="$t('dashboard.support.placeholder')"
        class="va-chat__input"
        type="textarea"
      />
      <div class="va-chat__controls_buttons">
        <va-button @click="handleSubmit" v-if="isDesktop">
          {{ !editing ? $t('buttons.send') : $t('buttons.edit') }}
        </va-button>
        <va-button @click="handleSubmit" v-if="!isDesktop">
          <va-icon
            :name="!editing ? 'fa fa-paper-plane' : 'fa fa-check'"
            color="warning"
          />
        </va-button>
        <va-button @click="deleteMessage()" v-if="editing" color="danger">
          <va-icon name="fa fa-trash" />
        </va-button>
      </div>
    </div>
  </div>
</template>

<script>
import { StickyScroll } from '../../../services/vuestic-ui/components'

export default {
  name: 'chat',
  directives: { StickyScroll },
  data() {
    return {
      editing: null,
      inputMessage: '',
    }
  },
  computed: {
    computedStyle() {
      return { backgroundColor: this.$themes.darkForeground, color: 'white' }
    },
    computedYoursStyle() {
      return { backgroundColor: this.$themes.info, color: 'white' }
    },
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  props: {
    value: Array,
  },
  methods: {
    handleSubmit() {
      this.editing ? this.saveMessage() : this.sendMessage()
    },
    startEditing(message) {
      if (!message.delivered) return

      this.editing = message
      this.inputMessage = message.text
    },
    saveMessage(message) {
      this.editing.text = this.inputMessage
      this.inputMessage = ''

      let updated = [...this.value]

      this.editing = null

      this.$emit('update-messages', updated)
    },
    deleteMessage() {
      let updated = this.value.filter(m => m != this.editing)
      this.value = updated
      this.$emit('update-messages', updated)

      this.editing = null
      this.inputMessage = ''
    },
    formatDate(ts) {
      if (typeof ts == 'string') return ts
      return this.$time.format(this.$time.UTCToLocale(ts))
    },
    scrollToBottom() {
      setTimeout(() => {
        var messages = document.querySelector('.va-chat__body')
        messages.scrollTop = messages.scrollHeight - messages.clientHeight
      }, 10)
    },
    sendMessage() {
      this.inputMessage = this.inputMessage.replace(/^[\n\s]+|[\n\s]+$/g, '')

      if (!this.inputMessage) {
        return
      }

      this.$emit('input', {
        text: this.inputMessage,
        delivered: false,
        yours: true,
        date: '',
      })

      setTimeout(() => {
        this.$el.querySelector('.va-chat__input').focus()
        this.inputMessage = ''
      }, 10)
    },
  },
  mounted() {
    this.$root.$on('chat-update', this.scrollToBottom)
    this.scrollToBottom()
  },
  destroy() {
    this.$root.$off('chat-update', this.scrollToBottom)
  },
}
</script>

<style lang="scss">
@import '../../../sass/smart-grid';

.chat {
  &__content {
    @include va-flex-center();
  }
}

$chat-message-br: 0.65rem;

.va-chat {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;

  &__body {
    padding: 20px 10px;
    width: 100%;
    height: 100%;
    min-height: 26rem;
    overflow-y: auto;

    &-messages {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      max-height: 300px;
    }
  }

  &__message {
    position: relative;
    padding: 0.657rem 1.375rem;
    padding-left: 2rem;
    margin-bottom: $chat-message-br;
    max-width: 70%;
    overflow-wrap: break-word;
    border-radius: 0.5rem;
    border-top-left-radius: 0;
    align-self: flex-start;
    background-color: $light-gray2;
    transition: padding 0.3s;

    &:hover .edit-message {
      display: block;
    }

    .edit-message {
      display: none;
      cursor: pointer;
      font-size: 0.9rem;
      opacity: 0.3;
      padding: 2px;

      &:hover {
        opacity: 1;
      }
    }

    &-info {
      position: absolute;
      top: 0.5rem;
      display: flex;
      font-size: 0.7rem;
    }

    &-text {
      display: block;
      text-align: left;
      transform: translateY(-2px);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &--yours {
      padding-right: 2rem;
      align-self: flex-end;
      border-top-right-radius: 0;
      border-top-left-radius: 0.5rem;
    }

    &_date {
      font-family: sans-serif;
      font-size: 0.8rem;
      opacity: 0;
      transition: opacity 0.5s;
    }
  }

  &__controls {
    display: flex;
    align-items: flex-start;
    height: 100%;
    width: 100%;
    margin: 0 0;
    margin-top: 1rem;

    &_buttons {
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;
    }
  }

  &__input {
    width: 100%;
    color: white;
  }
}
</style>
