import axios from 'axios'

const base = window.location.protocol + '//' + window.location.host

const fetchMessages = user =>
  axios.get(base + '/api/admin/support?user=' + user)
const sendMessage = (user, message) =>
  axios.post(base + '/api/admin/support/' + user + '/send', {
    message,
  })

const updateMessages = (messages, user) =>
  axios.post(base + '/api/admin/support/update', {
    messages,
    user,
  })

export { fetchMessages, sendMessage, updateMessages }
