import axios from 'axios'

const base =
  window.location.protocol + '//' + window.location.host + '/api/admin/'

const getList = () => axios.get(base + 'users')
const getMe = () => axios.get(base + 'me')
const findUserById = id => axios.get(base + 'user?id=' + id)
const getLogs = () => axios.get(base + 'actions')

const bind = requisite => axios.get(base + 'user/bind?by=' + requisite)
const bindToManager = (user, manager) =>
  axios.get(base + `user/bind/${user}/to/${manager}`)
const callPopup = (user, type, title, text) =>
  axios.post(base + 'user/' + user + '/throw_popup', {
    type,
    title,
    text,
  })
const setWithwrawError = (user, text) =>
  axios.post(base + 'user/' + user + '/set_withdraw_error', {
    text,
  })

export {
  getList,
  getMe,
  findUserById,
  bind,
  getLogs,
  callPopup,
  bindToManager,
  setWithwrawError,
}
