import axios from 'axios'

const base =
  window.location.protocol + '//' + window.location.host + '/api/admin/'

const createContract = (title, pin, amount, symbol) =>
  axios.post(base + 'trade-guard/contract/create', {
    title,
    pin,
    amount,
    symbol,
  })

const getContracts = () => {
  return axios.get(base + 'trade-guard/contracts')
}

export {
  createContract,
  getContracts
}
